
/* Untuk WebKit (Google Chrome, Safari, dll.) */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: #555; 
}

.progress-bar-container {
    position: absolute;
	font-family: "Lora", sans-serif; 
	font-weight: bold;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-color: #1e93d2ab;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#progress-bar {
    width: 30%;
    margin-top: 0.5%;
    height: 2%;
}

label {
    color: white;
    font-size: 2rem;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html, body, footer{
	position: absolute;
	cursor: none;
	margin: 0;
}


.custom-cursor {
	position: fixed;
	top: 0;
	left: 0;
	display: none;
 	 pointer-events: none; 
 	 z-index: 1000;
  
}

.site-wide {
	width: 30px;
	height: 30px;
	border-radius: 50%;
	border: 2px solid black;
	z-index: 1000;
	pointer-events: none;
  
}

.site-wide .pointer {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 5px;
	height: 5px;
	border-radius: 50%;
	background-color: black;
	transition: width 0.1s ease-in-out, height 0.1s ease-in-out;
	z-index: 1000;
	pointer-events: none;

}

.site-wide.active .pointer {
	width: 20px;
	height: 20px;
	pointer-events: none;
	z-index: 1000;
}

