.tooltip {
    position: relative;
	font-size: 14px;
	font-family: "Lora", sans-serif; 
	font-weight: bold;
    background-color: #0077b6;
	outline-color: white;
	outline-style: solid;
	outline-width: 2px;
	line-height: 22px;
	text-align: center;
    color: white;
    padding: 15px;
	padding-left: 25px;
	padding-right: 25px;
    border-radius: 15px;
	max-width: 222px;
	max-inline-size: 222px;
	opacity: 0;
	transform: translateY(-10px);
	transition-duration: 0.2s;
	transition-property: opacity, transform;
  }

.hide {
	opacity: 0;
	transform: translateY(-30px);
}

.tip360 {
    position: relative;
	font-size: 18px;
	font-family: "Lora", sans-serif; 
	font-weight:bolder;
	text-align: center;
    color: white;
    padding: 15px;
	opacity: 0;
	transform: translateY(-10px);
	transition-duration: 0.2s;
	transition-property: opacity, transform;
  }


.show {
	opacity: 1;
    transform: translateY(-30px);
  }

.text-page {
    position: fixed;
    top: 0px;
	text-align: justify;
    right: 0;
    width: 35%;
    height: 100%;
    background-color: white;
    padding: 42px;
	display:block;
    box-shadow: -2px 0 5px rgba(0,0,0,0.5);
	right: -100%;
	overflow-y: auto;
	max-height: 100vh;
	transition: right 0.3s ease-in-out;
	z-index: 200;
  }

.text-page h1 {
	font-size: 40px;
	font-weight: 900;
	text-align: left;
	padding-top: 25px;
	font-family: "Lora", sans-serif; 
    color: #0297e7;
}

.text-page p{
	font-size: 16px;
	line-height: 20px;
	padding-bottom: 15px;
	font-family: "Lora",serif; 
}
.text-page a{
      color:#0077b6;
	  font-family: "Lora",serif; 
	  text-decoration: none;
      background-color: transparent;
	  cursor:-webkit-grab;
	  font-size: 16px;
	  line-height: 20px;
}

.text-page a:hover{
	color:#0077b6;
	text-decoration:underline;
}

  .text-page.show {
    position: fixed;
	display: block;
	top: 30px;
	height: 100%;
	right: 0;

}
	.text-page img{
	margin-top: 30px;
	margin-bottom: 30px;

	}

  .compass {
	position: absolute;
	cursor: none;
	top: 16px;
	right: 60px;
	width: 70px;
	height:70px;
	background: url('../image/compass.png') no-repeat center center / contain;
	transform-origin: center center;
	pointer-events:all;
}

.icon-360 {
	position: absolute;
	width: 25px;
	height: 25px;
	transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
	z-index: -10;
}

.icon-360.zoom{
	width: 40px;
	height: 40px;
	transform: scale(1.5);
}
	


#guide {
	position: absolute;
	width: 0%;
	height: 0%;
	margin-left: 32.6%;
	margin-top: 15.5%;
	opacity: 0;
  	transition: opacity 0.5s ease-in-out, width 0.5s ease-in-out, height 0.5s ease-in-out;
	transition-delay: 2s;
	z-index: 10;
	pointer-events: none;
}

#guide.visible {
	opacity: 1;
	z-index: 10;
	width: 35%;
	height: 35%;
}

#guide.hidden{
	opacity: 0;
	display: none;
}	

.panorama {
    position: fixed;
	top: 0px;
	text-align: justify;
    width: 100%;
    height: 100%;
    background-color: #161616e7;
    padding: 80px;
	display:block;
	opacity: 0;
	transition: opacity 0.5s ease-in-out;
	z-index: 100;
	pointer-events: none;
  }

  .panorama.show {
	opacity: 1;
    position: fixed;
	display: block;
	top: 30px;
	pointer-events: all;
  }

.ku-embed {
	width: 100%;
    height: 100%;
	z-index: 1;

}

